import React, { useEffect, useState } from 'react';
import './LoanBox.scss';
import { motion } from "framer-motion";
import { useDispatch, useSelector } from 'react-redux';
import { usersReducer } from './../../store/reducers/usersReducer';
import { loadUser } from '../../store/actions/userActions';
import { loanReducer } from './../../store/reducers/loanReducer';
import { setCurrentLoanSelected } from '../../store/actions/loanActions';

export default function LoanBox() {
    const [numOfPayments, setPayments] = useState([]);
    // const user = useSelector(state => state.usersReducer.user);
    const dispatch = useDispatch();
    const userId = useSelector(state => state.usersReducer.user);
    const user = useSelector(state => state.usersReducer.userData);
    const currentLoanSelected = useSelector(state => state.loanReducer.currentLoanSelected);


    useEffect(() => {
        dispatch(loadUser(userId._id));
        dispatch(setCurrentLoanSelected(currentLoanSelected));
    }, [])
    useEffect(() => {
        if (currentLoanSelected) {
            setPayments([...Array(currentLoanSelected.maxNumOfPayments).keys()].map(i => i + 1));
        }
    }, [currentLoanSelected])


    const formatNum = (num) => {
        return num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    }

    const pageTransition = {
        in: {
            opacity: 1,
            // y: 0,
            transition: { duration: 0.8 }
        },
        out: {
            opacity: 0,
            // y: "-100vh",
        }
    };

    return (
        <div className="loan-box">
            <div className="svg-bgc">
                <svg data-name="Layer 1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1200 120" preserveAspectRatio="none">
                    <path d="M0,0V46.29c47.79,22.2,103.59,32.17,158,28,70.36-5.37,136.33-33.31,206.8-37.5C438.64,32.43,512.34,53.67,583,72.05c69.27,18,138.3,24.88,209.4,13.08,36.15-6,69.85-17.84,104.45-29.34C989.49,25,1113-14.29,1200,52.47V0Z" opacity=".25" className="shape-fill"></path>
                    <path d="M0,0V15.81C13,36.92,27.64,56.86,47.69,72.05,99.41,111.27,165,111,224.58,91.58c31.15-10.15,60.09-26.07,89.67-39.8,40.92-19,84.73-46,130.83-49.67,36.26-2.85,70.9,9.42,98.6,31.56,31.77,25.39,62.32,62,103.63,73,40.44,10.79,81.35-6.69,119.13-24.28s75.16-39,116.92-43.05c59.73-5.85,113.28,22.88,168.9,38.84,30.2,8.66,59,6.17,87.09-7.5,22.43-10.89,48-26.93,60.65-49.24V0Z" opacity=".4" className="shape-fill"></path>
                    <path d="M0,0V5.63C149.93,59,314.09,71.32,475.83,42.57c43-7.64,84.23-20.12,127.61-26.46,59-8.63,112.48,12.24,165.56,35.4C827.93,77.22,886,95.24,951.2,90c86.53-7,172.46-45.71,248.8-84.81V0Z" className="shape-fill"></path>
                </svg>
            </div>
            {currentLoanSelected.name &&
                <ul className="inner">
                    <h1>{currentLoanSelected.name} Program</h1>






                    <div>
                        <li>{currentLoanSelected.name} loan program yearly interest is {currentLoanSelected.interest}%.</li>
                        <li>Risk management interest is {currentLoanSelected.riskInterest}%.</li>
                        <li>Market interest is {currentLoanSelected.marketInterest}%.</li>
                        <li>Prime interest is {currentLoanSelected.prime}%.</li>
                    </div>


                    <div>
                        <li>Minimum loan amount for the {currentLoanSelected.name} program is ${formatNum(currentLoanSelected.minLoanAmount)}</li>
                        <li>Maximum loan amount for the {currentLoanSelected.name} program is ${formatNum(currentLoanSelected.maxLoanAmount)}</li>
                        <li>Maximum number of payments for the {currentLoanSelected.name} program is {numOfPayments[numOfPayments.length - 1]}.</li>
                    </div>



                </ul>
            }

        </div>
    )
}



